import React from 'react';
import { Plugin, NodeSelectToolbarItem } from '@ali/4ever-cangjie';
import { MobileIconCopy, MobileIconCut, MobileIconDelete } from '@ali/4ever-component';
import { ILocale } from '../common/types';

export const createGetNodeSelectToolbarItems = (locale: ILocale): Plugin['getNodeSelectToolbarItems'] => {
  return (props, _, next) => {
    const copyItem: NodeSelectToolbarItem = {
      key: 'copy',
      icon: <MobileIconCopy color="rgb(92, 113, 221)" />,
      action: 'copySelectedNodes',
      priority: 3,
      title: locale.copy || 'Copy',
    };
    const cutItem: NodeSelectToolbarItem = {
      key: 'cut',
      icon: <MobileIconCut color="rgb(75, 173, 112)" />,
      action: 'cutSelectedNodes',
      priority: 2,
      title: locale.cut || 'Cut',
    };
    const deleteItem: NodeSelectToolbarItem = {
      key: 'delete',
      icon: <MobileIconDelete color="rgb(228, 117, 117)" />,
      action: 'deleteSelectedNodes',
      priority: 4,
      title: locale.delete || 'Delete',
    };
    const res = [copyItem, cutItem, deleteItem];
    const nextItems = next();
    return [...res, ...(Array.isArray(nextItems) ? (nextItems) : [])];
  };
};
